#works{
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}
.worksTitle{
    margin: 1rem 0;
    font-size: 3rem;
}
.worksDesc{
    font-weight: 300;
    font-size: 1rem;
}
.worksImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    max-width: 65rem;
    flex-wrap: wrap;
}
.worksImg{
    object-fit: cover;
    height: 17rem;
    width: 25rem;
    padding: 16px;
    margin: 0.5rem;
}
.worksBtn{
    margin: 2.6rem 0;
    padding: 0.5rem 2.5rem;
    border: none;
    background-color: white;
    border-radius: 2rem;
    font-size: 1rem;
    cursor: pointer;
}

@media screen and (max-width:585px){
    .worksImg{
        height:48vw;
    }
    
}